import React, { useState, useEffect, useMemo, useCallback } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import './OrderPage.css';
import { Form, Input, Select, message } from 'antd';
import OrderPopup from './OrderPopup';

const { Option } = Select;
const tg = window.Telegram.WebApp;

function OrderPage({ webAppQueryId }) {
    const [form] = Form.useForm();
    const [showPopup, setShowPopup] = useState(false);
    const [orderItems, setOrderItems] = useState([]);
    const [orderDetails, setOrderDetails] = useState(null);
    const location = useLocation();
    const navigate = useNavigate();
    const pickupAddress = 'Mui ne, Ocean vista, block B';

    //   
    const unitMapping = {
        cheese: '',
        fish: '',
        lemon: '',
    };

    //  
    const toppingsMapping = {
        sourCream: '',
        condensedMilk: '',
        passionFruitJam: '  ',
    };

    //    sessionStorage
    useEffect(() => {
        const savedOrder = JSON.parse(sessionStorage.getItem('currentOrder')) || [];
        setOrderItems(savedOrder);
    }, []);

    //    sessionStorage  
    useEffect(() => {
        sessionStorage.setItem('currentOrder', JSON.stringify(orderItems));
    }, [orderItems]);

    //      location  sessionStorage
    const orderData = useMemo(() => {
        return (
            location.state?.orderData || //    state,  
            (location.pathname === '/order' && JSON.parse(sessionStorage.getItem('fishOrderData'))) || //   FishPage
            (location.pathname === '/order' && JSON.parse(sessionStorage.getItem('cheeseOrderData'))) || //   CheesePage
            (location.pathname === '/order' && JSON.parse(sessionStorage.getItem('lemonOrderData'))) || //   LemonPage
            {}
        );
    }, [location.state, location.pathname]);

    //      
    useEffect(() => {
        if (orderData?.quantity && orderData?.category) {
            const newOrderItem = {
                id: `${orderData.type}-${orderData.category}`,
                title: orderData.category, //  ""  
                quantity: orderData.quantity,
                price: orderData.price,
                image: orderData.image,
                toppings: orderData.toppings || [],
                type: orderData.type,
            };

            setOrderItems((prevItems) => {
                const existingItemIndex = prevItems.findIndex((item) => item.id === newOrderItem.id);
                if (existingItemIndex !== -1) {
                    const updatedItems = [...prevItems];
                    updatedItems[existingItemIndex] = newOrderItem; //   
                    return updatedItems;
                }
                return [...prevItems, newOrderItem];
            });
        }
    }, [orderData]);

    const totalPrice = useMemo(() => {
        return orderItems.reduce((sum, item) => sum + (item.price * item.quantity || 0), 0);
    }, [orderItems]);

    const handleOrderSubmit = useCallback(
        (values) => {
            const details = {
                ...values,
                address: values.deliveryMethod === 'delivery' ? values.address : pickupAddress,
                items: orderItems.map((item) => ({
                    ...item,
                    total: (item.price * item.quantity).toFixed(2),
                })),
                totalPrice: totalPrice.toFixed(2),
            };

            tg.sendData(JSON.stringify(details));
            setOrderDetails(details);
            setShowPopup(true);
            message.success('  !');
        },
        [pickupAddress, orderItems, totalPrice]
    );

    return (
        <>
            <div className="order-container">
                <div className="order-details">
                    {orderItems.map((item, index) => (
                        <div key={item.id} className="order-item">
                            <img src={item.image || '../../images/fish.webp'} alt={item.title}
                                 className="order-item-image"/>
                            <div className="order-item-info">
                                <h3>
                                    {item.title}
                                </h3>

                                <p>: {item.quantity}{unitMapping[item.type]}</p>
                                {item.toppings.length > 0 && (
                                    <p>: {item.toppings.map((topping) => toppingsMapping[topping] || topping).join(', ')}</p>
                                )}
                                <p>: {item.price && !isNaN(item.price) ? Number(item.price).toLocaleString('ru-RU') : '0'} VND</p>
                                {index < orderItems.length - 1 && <hr/>}
                                <div className="order-item-actions">
                                    <button
                                        className="edit-button"
                                        onClick={() => {
                                            //        
                                            sessionStorage.setItem(`${item.type}OrderData`, JSON.stringify(item));
                                            navigate(`/${item.type}`);
                                        }}
                                    >
                                        
                                    </button>
                                    <button
                                        className="delete-button"
                                        onClick={() => {
                                            //     
                                            setOrderItems((prevItems) => prevItems.filter((_, i) => i !== index));
                                        }}
                                    >
                                        
                                    </button>
                                </div>
                            </div>
                        </div>
                    ))}
                    <h2> : {totalPrice > 0 ? totalPrice.toLocaleString('ru-RU') : '0'} VND</h2>
                </div>

                {showPopup && (
                    <OrderPopup onClose={() => tg.close()} orderDetails={orderDetails} webAppQueryId={webAppQueryId}/>
                )}
            </div>

            <h3 className="add-order-header">  </h3>
            <div className="order-buttons">
                <button onClick={() => navigate('/cheese')}></button>
                <button onClick={() => navigate('/fish')}></button>
                <button onClick={() => navigate('/lemon')}></button>
            </div>

            <div className="order-form">
                <h3>  </h3>
                <Form layout="vertical" form={form} onFinish={handleOrderSubmit}>
                    <Form.Item label="" name="name" rules={[{ required: true, message: ' ' }]}>
                        <Input placeholder="  " />
                    </Form.Item>
                    <Form.Item
                        label=""
                        name="phone"
                        rules={[
                            { required: true, pattern: /^\+?\d{10,15}$/, message: '   ' },
                        ]}
                    >
                        <Input placeholder="  " />
                    </Form.Item>
                    <Form.Item label=" " name="deliveryMethod" initialValue="pickup">
                        <Select>
                            <Option value="pickup"></Option>
                            <Option value="delivery"></Option>
                        </Select>
                    </Form.Item>
                    <Form.Item shouldUpdate>
                        {({ getFieldValue }) => {
                            const deliveryMethod = getFieldValue('deliveryMethod') || 'pickup';
                            return deliveryMethod === 'delivery' ? (
                                <Form.Item
                                    label=" "
                                    name="address"
                                    rules={[{ required: true, message: '  ' }]}
                                >
                                    <Input placeholder="  " />
                                </Form.Item>
                            ) : (
                                <p className="pickup-address">: {pickupAddress}</p>
                            );
                        }}
                    </Form.Item>
                </Form>
            </div>
        </>
    );
}

export default OrderPage;
